import React from 'react'
import { BrowserRouter as Router, Route, Switch} from 'react-router-dom';
import { Nav, Navbar, Button, NavDropdown, Form, FormControl, Container } from 'react-bootstrap';
import Home from './Home'
import Registrar from './Registrar'
import Login from './Login'
import Contactus from './Contactus'
import About from './About'
import PageNotFound from './PageNotFound'

const NavBar = () => {
    return (
        <Router >
            <Navbar bg="light" expand="lg" sticky ="top">
                <Container fluid>
                    <Navbar.Brand href="/">Hardware Gaming</Navbar.Brand>
                    <Navbar.Toggle aria-controls="navbarScroll" />
                    <Navbar.Collapse id="navbarScroll">
                    <Nav
                        className="me-auto my-2 my-lg-0"
                        style={{ maxHeight: '100px' }}
                        navbarScroll
                    >
                        <Nav.Link href="/about">Sobre nosotros</Nav.Link>
                        <Nav.Link href="/contactus">Contactenos</Nav.Link>
                        <NavDropdown title="Link" id="navbarScrollingDropdown">
                        <NavDropdown.Item href="#action3">Action</NavDropdown.Item>
                        <NavDropdown.Item href="#action4">Another action</NavDropdown.Item>
                        <NavDropdown.Divider />
                        <NavDropdown.Item href="#action5">
                            Something else here
                        </NavDropdown.Item>
                        </NavDropdown>
                        <Nav.Link href="#" disabled>
                        Link
                        </Nav.Link>
                    </Nav>
                    <Form className="d-flex">
                        <FormControl
                        type="search"
                        placeholder="Search"
                        className="me-2"
                        aria-label="Search"
                        />
                        <Button variant="outline-success">Search</Button>
                    </Form>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
            <Switch>
            <Route exact path="/"  component={Home}> </Route>
            <Route exact path="/login" component={Login} > </Route>
            <Route exact path="/registrar" component={Registrar} ></Route>
            <Route exact path="/about" component={About} ></Route>
            <Route exact path="/contactus" component={Contactus} ></Route>
            <Route component={PageNotFound}> </Route>
            </Switch>
      
        </Router>
    )
}

export default NavBar
